@import "imports/reset", "imports/mixin", "imports/variables";

/*encabezado*/
.encabezado{
  background-color: blueviolet ;
}

.ultimo_li{
  padding-right: 100px;
}
header img{
  max-width: 150px;
  padding-left: 20px;
}
header ul{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #cecece;
}





/*main*/

.cajita img{
  max-height: 200px;
}



.caja{
  margin-left: 5%;
  margin-top: 50px;
  width: 90%;
  max-width: 120em;
  background-color: #b6b6b6;
  padding: 1em;
  @include neo( 1 , 1em , 1em)
}


.caja .ul{
  width: 100%;
  //background-color: aqua;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}


.caja .li{
  background-color: aliceblue;
  padding: 1em;
  margin: (0 1em 0 0);
  @include neo( 1 , 1em , 1em);
}

.caja .li.activo{
  background-color: yellow;
}


.caja .bloque_abajo{
  //background-color: blue;
  position: relative;
  min-height: 10vh;
}


.caja .cajita{
  background-color: blueviolet;
  margin: 1em;
  padding: 1em;
  position: absolute;
  top: 0;
  @include neo(1,1em,.5em);
  opacity: 0;
}


.caja .cajita.activo{
  opacity: 1;
}